<script>

import loadXMLDoc from '@/store/AjaxRequest';

export default {
    data() {
        return {
            userarr: [{
                username: "",
                useremail: "",
                userphone: "",
                userdate: "",
            }],
            num: 0,
        }
    },
    created() {
        // 信息
        loadXMLDoc({
            reqBody: {
                'token': localStorage.getItem("token"),
                'deviceid': localStorage.getItem("equipment")
            },
            reqUrl: 'http://8.148.31.153:3030/getInfos',
            callback: (error, resData) => {
                try {
                    const data = JSON.parse(resData)
                    if (!error) {
                        if (data.success) {
                            this.userarr.push({
                                username: data.resData.USERNAME,
                                useremail: data.resData.EMAIL_ADDRESS,
                                userphone: data.resData.PHONE,
                                userdate: data.resData.JOIN_DATE,
                            })
                            this.num = 1
                        } else {
                            this.$store.commit("tanchaung", "验证已过期，请重新登录")
                            this.$store.commit("tanchuangshow", true)
                        }
                    }
                } catch (error) {
                    console.log(error)
                }
            }
        })
    },

    methods: {
        del() {
            console.log("1")
            // 删除账号
            loadXMLDoc({
                reqBody: { 'token': localStorage.getItem("token"), 'deviceid': localStorage.getItem("equipment") },
                reqUrl: "http://8.148.31.153:3030/remInfos",
                callback(error, resData) {
                    try {
                        if (!error) {
                            this.$store.commit("tanchaung", "注销成功")
                            this.$store.commit("tanchuangshow", true)
                            console.log(resData)
                            localStorage.clear();
                        }
                    } catch (error) {
                        console.log(error.message)
                    }
                }
            })
        }
    }
};
</script>

